.clearfix {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}
.lay-editor-wrapper {
  width: 100%;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  font-family: "Chinese Quote", -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-variant: tabular-nums;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  font-size: 1rem;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.65);
  background-color: #fff;
  background-image: none;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.lay-editor-wrapper h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
.lay-editor-wrapper blockquote {
  border-left: 5px solid #eee;
  color: #666;
  font-style: italic;
  margin: 16px 0;
  padding: 10px 20px;
  background-color: #fbfbfb;
}
.lay-editor-wrapper pre {
  background: #f1f1f1;
  border-radius: 3px;
  padding: 1px 10px;
  font-family: Inconsolata,Menlo,Consolas,monospace;
}
.lay-editor-toolbar-wrapper {
  background: #f5f5f5;
  padding: 10px 10px;
}
.lay-editor-toolbar-wrapper:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}
.lay-editor-inline-wrapper,
.lay-editor-block-wrapper,
.lay-editor-list-wrapper,
.lay-editor-align-wrapper,
.lay-editor-fontsize-wrapper,
.lay-editor-remove-wrapper,
.lay-editor-history-wrapper {
  display: inline;
  margin: 5px 0;
  border-left: 1px solid #e2e2e2;
}
.lay-editor-content-wrapper {
  padding: 4px 11px;
}
.lay-editor-icon {
  display: inline-block;
  font-style: normal;
  vertical-align: -0.125em;
  text-align: center;
  text-transform: none;
  line-height: 0;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}
.lay-editor-icon svg {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentcolor;
}
.lay-editor-tool-btn {
  display: inline;
  padding: 5px 10px;
  cursor: pointer;
}
.lay-editor-tool-btn:hover {
  background: #e6e6e6;
}
.lay-editor-tool-btn-active {
  color: #1890ff;
}
.lay-editor-tool-btn-disabled {
  color: #ccc;
}
.lay-editor-tool-btn-disabled:hover {
  background: #f5f5f5;
}
.lay-editor-left-aligned-block {
  text-align: left;
}
.lay-editor-left-aligned-block > div {
  display: inline-block;
}
.lay-editor-center-aligned-block {
  text-align: center;
}
.lay-editor-center-aligned-block > div {
  display: inline-block;
}
.lay-editor-right-aligned-block {
  text-align: right;
}
.lay-editor-right-aligned-block > div {
  display: inline-block;
}
.lay-editor-justify-aligned-block {
  text-align: justify;
}
.lay-editor-justify-aligned-block > div {
  display: inline-block;
}
.rc-tooltip-inner {
  min-height: 0;
}
.rc-tooltip.rc-tooltip-fade-enter,
.rc-tooltip.rc-tooltip-fade-leave {
  display: block;
}
.rc-tooltip-fade-enter,
.rc-tooltip-fade-appear {
  opacity: 0;
  animation-duration: 0.15s;
  animation-fill-mode: both;
  animation-timing-function: ease-in;
  animation-play-state: paused;
}
.rc-tooltip-fade-leave {
  animation-duration: 0.15s;
  animation-fill-mode: both;
  animation-timing-function: ease-out;
  animation-play-state: paused;
}
.rc-tooltip-fade-enter.rc-tooltip-fade-enter-active,
.rc-tooltip-fade-appear.rc-tooltip-fade-appear-active {
  animation-name: rcToolTipFadeIn;
  animation-play-state: running;
}
.rc-tooltip-fade-leave.rc-tooltip-fade-leave-active {
  animation-name: rcToolTipFadeOut;
  animation-play-state: running;
}
@keyframes rcToolTipFadeIn {
  0% {
    opacity: 0;
    transform-origin: 50% 50%;
  }
  100% {
    opacity: 1;
    transform-origin: 50% 50%;
  }
}
@keyframes rcToolTipFadeOut {
  0% {
    opacity: 1;
    transform-origin: 50% 50%;
  }
  100% {
    opacity: 0;
    transform-origin: 50% 50%;
  }
}
.rc-dropdown-menu > .rc-dropdown-menu-item {
  position: relative;
  display: block;
  width: 50px;
  padding: 5px 12px;
  line-height: 22px;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.65);
  white-space: nowrap;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-transition: background .3s ease;
  transition: background .3s ease;
  font-size: 14px;
}
.rc-dropdown-menu > .rc-dropdown-menu-item-selected:hover,
.rc-dropdown-menu > .rc-dropdown-menu-item-active,
.rc-dropdown-menu > .rc-dropdown-menu-item-selected {
  background-color: #fafafa;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.65);
}



